export default {
  methods: {
    formCheck(form) {
      let pass;
      const comp = this.$refs[form];

      if (comp.formCheck) {
        comp.formCheck();
      } else {
        comp.validate(valid => {
          pass = valid;
        });
      }
      return pass;
    },
    async clearCheck(form) {
      // 必须使用nextTick保证在弹窗出现后执行
      await this.$nextTick();
      const comp = this.$refs[form];
      if (!comp) return;
      if (comp.clearCheck) {
        comp.clearCheck();
      } else {
        comp.clearValidate();
      }
    },
  },
};
